.content {
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 313px;
}

.link {
    text-decoration: none;
    box-sizing: border-box;
    line-height: 30px;
    margin: 17px 0;
    cursor: pointer;
    color: #8585ad;
    opacity: 1;
}

.link:hover {
    transition: opacity 0.15s ease-in-out;
    opacity: 0.8;
}

.text {
    color: #8585ad;
    opacity: 0.4;
    margin-top: 80px;
}

.link__active {
    color: #f2f2f3;
}

.logout__btn {
    box-sizing: border-box;
    line-height: 30px;
    margin: 17px 0;
    padding: 0;
    border: 0;
    text-align: left;
    cursor: pointer;
    color: #8585ad;
    background-color: transparent;
}

.logout__btn:hover {
    transition: opacity 0.15s ease-in-out;
    opacity: 0.8;
}
