.section {
    width: 600px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    border-width: 2px;
    border-style: solid;
    border-radius: 3%;
}

.constructorEl__container {
    width: 568px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

.constructorEl__container_main {
    width: 594px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    max-height: 60vh;
    overflow-y: auto;
    scrollbar-color: #8585ad #2f2f37;
    scrollbar-width: initial;
    scroll-behavior: smooth;
}

.constructorEl__list {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.constructor__price_container {
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;
    box-sizing: border-box;
    margin: 40px 0;
}

.constructor__price {
    display: block;
    box-sizing: border-box;
    margin: 0 10px 0 0;
    padding: 0;
}

.constructor__price_container svg {
    height: 33px;
    width: 33px;
}

.constructor__price_container button {
    margin-left: 40px;
}
