.card__container {
    position: relative;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    width: 272px;
    padding: 0;
    margin: 0;
    border: 0;
    cursor: pointer;
    background-color: #131316;
    color: #f2f2f3;
    overflow: hidden;
    text-decoration: none;
}

.card__img {
    display: block;
    box-sizing: border-box;
    width: 100%;
}

.card__price_container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    box-sizing: border-box;
    margin: 4px auto;
}

.card__price {
    display: block;
    box-sizing: border-box;
    margin: 0 9px 0 0;
    padding: 0;
}

.card__name {
    text-align: center;
}
